import {
  CLONE_PEOPLE,
  GET_PAIR,
  UPDATE_LADDER,
  MAKE_LOG,
  CLEAR_LOG
} from '../constants/vkvote_const'

const initialState = {
  peopleVote: [],
  pair: [],
  logs: []
};

export default function vkvote(state = initialState, action) {
  console.log(action.type);
  switch (action.type) {
    case CLONE_PEOPLE:
      return { ...state, peopleVote: action.payload };
    case GET_PAIR:
      return { ...state, pair: action.payload };
    case UPDATE_LADDER:
      return { ...state, peopleVote: updatePeople(action.payload.winer, action.payload.loser, state.peopleVote) };
    case MAKE_LOG:
      return { ...state, logs: state.logs.concat(action.payload) };
    case CLEAR_LOG:
      return { ...state, logs: [] };
    default:
      return state;
  }
}

//legacy of v0.2
function updatePeople(winer, loser, currentLadder) {
  const newLadder = currentLadder.map(person => {
    if(person.uid === winer.uid) {
      person.wins = [...person.wins, loser.uid];
    } else if(person.uid === loser.uid) {
      person.loses = [...person.loses, winer.uid];
    }
    return person;
  });

  newLadder.forEach(person => {
    person.wins.forEach(uid => {
      let winsOfLoser = getByUid(uid, newLadder).wins;
      person.wins = [...person.wins, ...addOnlySpecial(person.wins, winsOfLoser)];
    });
    person.loses.forEach(uid => {
      let losesOfWiner = getByUid(uid, newLadder).loses;
      person.loses = [...person.loses, ...addOnlySpecial(person.loses, losesOfWiner)];
    });
  });

  return newLadder;
}

function getByUid(uid,arr) {
  let result = null;
  for(let i = 0; i < arr.length; i++) {
    if(uid === arr[i].uid) {
      return arr[i];
    }
  }
  return result;
}

function addOnlySpecial(arr, addArr) {
  let result = [];
  addArr.forEach(uid => {
    if(!arr.includes(uid)) {
      result.push(uid);
    }
  });
  return result;
}



