import { StyleSheet } from 'aphrodite/no-important'

import { red } from './const_styl'

const footer =  StyleSheet.create({
  footer: {
    backgroundColor: red,
    height: '60px',
    color: '#fff',
    padding: '0px 100px'
  }
});

export default footer



